.header_outer_container {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(20, 20, 20);
/* background-color: transparent; */
height: 8vh;
color: white;
transition: all 0.5s;
transition-timing-function: ease-in;
z-index: 1;
/* background-color: red; */
opacity: 0.7;
}
.nav_black {
    background-color: rgb(20, 20, 20);
}
.header_container{
width: 90%;
display: flex;
justify-content: space-between;
align-items: center;
}

.header_left ul {
    width: 140%;
    display: flex;
    align-items: center;
    list-style: none;
    list-style: none;
    justify-content: space-between;
    cursor: pointer;
}

.header-right ul {
    width: 140%;
    display: flex;
    align-items: center;
    list-style: none;
    list-style: none;
    justify-content: space-between;
}